import {Component} from 'react' 
import {AiFillDelete} from 'react-icons/ai'
import {FaRegEdit} from 'react-icons/fa'
import {TiCancel} from 'react-icons/ti'
import {VscSaveAs} from 'react-icons/vsc'
import './index.css'
class XYZ extends Component{
    state={EditInput:''}
    Delete=()=>{
        this.props.onDelete(this.props.id)
    } 
    Edit=()=>{
        this.props.onEdit(this.props.id)
    }
    Save=()=>{
        const text=this.state.EditInput
        this.state.EditInput=''
        this.props.onSave(this.props.id,text)
    }
    Cancel=()=>{
        this.state.EditInput=''
        this.props.onCancel(this.props.id)
    }
    onChangeEditInput=event=>{
        console.log(this.state.EditInput)
        this.setState({
            EditInput: event.target.value,
          })
    }
    render() {
        return (
            <div className="todo-card">
                <h1 className="task">{this.props.name}</h1>
                <div className="edit-button">
                    <div className="z">
                      <AiFillDelete onClick={this.Delete} size={24}/>
                    </div>
                    {!this.props.click &&
                    <div className="z">
                       <FaRegEdit onClick={this.Edit} size={22}/>
                    </div>
                     }
                </div>
                { this.props.click&&<Card EditInput={this.state.EditInput} Cancel={this.Cancel} Save={this.Save} onChangeEditInput={this.onChangeEditInput}/>}
            </div>
        )
    }
}

const Card=props=>{
    const {EditInput,Cancel,Save,onChangeEditInput}=props
    return(
        <div className="y">
            <input type="text" placeholder="Rename"className="task-bar1"onChange={onChangeEditInput} value={EditInput}/>
            <div className="z">
               <TiCancel onClick={Cancel} size={26}/>
            </div>
            <div className="z">
                <VscSaveAs onClick={Save} size={26}/>
            </div>
        </div>
    )
}




export default XYZ