import { Link } from "react-router-dom";
import './index.css'
import Clock from "../Clock";

const Header=()=>{
    return(
        <div className="textAlign">
         <Clock/>
        <div className="yz">
            <div className="home-link"><Link className="home" to="/">Home</Link></div>
            <div className="todo-link"><Link className="todo" to="/Todo">ToDo</Link></div>
            <div className="diary-link"><Link className="diary" to="/Diary">Diary</Link></div>    
         </div>
         </div>
    )
}

export default Header