import {Component} from 'react' 
import './App.css'
import XYZ from './Components/XYZ/index'
import Home from './Components/Home'
import Clock from './Components/Clock'
import { BrowserRouter, Route, Switch } from "react-router-dom"
import Header from './Components/Header'
import PQ from './Components/PQ/index'
import {AiFillDelete} from 'react-icons/ai'

class App extends Component{
  render(){
    return(
      <BrowserRouter>
      <Header />
       <div className="background">
       <Switch>
         <Route exact path="/" component={Home} />
         <Route exact path="/Todo" component={Todo} />
         <Route exact path="/Diary" component={Diary}/>
       </Switch>
       </div>
        
      </BrowserRouter>
      
    )
  }
}
class Todo extends Component {
  state = {
    searchInput: '',todolist:(JSON.parse(localStorage.getItem("TodoMaterial"))==null)?[]:JSON.parse(localStorage.getItem("TodoMaterial")),priorInput:''
  }

  onChangeSearchInput = event => {
    console.log(event.target.value)
    this.setState({
      searchInput: event.target.value,
    })
  }
  onChangePriorInput = event => {
    console.log(event.target.value)
    this.setState({
      priorInput: event.target.value,
    })
  }
  onAddInput=()=>{

    const {searchInput,todolist,priorInput}=this.state
    const arr=todolist
    arr.push({prior:priorInput,name:searchInput,id:arr.length,click:false})
    for(let i=0;i<arr.length;i++){
      for(let j=i+1;j<arr.length;j++){
        const temp1=arr[i]
        const temp2=arr[j]
        if(parseInt(arr[i].prior)<parseInt(arr[j].prior)){
          arr[i]={prior:temp2.prior,name:temp2.name,id:temp1.id,click:false}
          arr[j]={prior:temp1.prior,name:temp1.name,id:temp2.id,click:false}
        }
      }
    }
    localStorage.setItem("TodoMaterial",JSON.stringify(arr))
    this.setState({todolist:arr,searchInput:'',priorInput:''})
  }
  onDelete=(id)=>{
    console.log('delete',id)
    const {searchInput,todolist}=this.state
    const arr=todolist
    for (let i=id;i<arr.length;i++){
        arr[i].id--;
    }
    arr.splice(id,1)
    localStorage.setItem("TodoMaterial",JSON.stringify(arr))
    this.setState({todolist:arr})
  }
  onEdit=(id)=>{
    console.log("EDIT triggered")
    const {searchInput,todolist}=this.state
    const ar=todolist
    ar[id].click=true
    localStorage.setItem("TodoMaterial",JSON.stringify(ar))
    this.setState({todolist:ar})
  }
  onCancel=(id)=>{
    const {searchInput,todolist}=this.state
    const ar=todolist
    ar[id].click=false
    localStorage.setItem("TodoMaterial",JSON.stringify(ar))
    this.setState({todolist:ar})
  }
  onSave=(id,text)=>{
    const {searchInput,todolist}=this.state
    const ar=todolist
    ar[id].click=false
    ar[id].name=text
    localStorage.setItem("TodoMaterial",JSON.stringify(ar))
    this.setState({todolist:ar})
  }
  
  render() {
    const {searchInput} = this.state
    const stringifiedTodoList = localStorage.getItem("TodoMaterial");
    const temp=JSON.parse(stringifiedTodoList)
    const todolist=temp
    let len=0;
    if(todolist!=null)
    {
      len=todolist.length
    }
    console.log(len)
    return (
       <div>
          <div className="bg-container">
              <h1 className="todo-list-heading">TODOLIST</h1>
              <p className="todo-para">Makes Life Easier...</p>
              <div className="todo-page-container">
              <div>
              <img alt="todo"src="https://res.cloudinary.com/du7d2nmbw/image/upload/v1626531473/054YhGcdSr0FkY8pIDnot1L-6..1622815566_cbzfg9.jpg" className="image"/>
              </div>
              <div className="todo-container">
                  <div>
                      <input placeholder="Enter Your Task"
                      className="task-bar" type="text" onChange={this.onChangeSearchInput} value={this.state.searchInput}/>
                      <input placeholder="/10"
                      className="priority-bar" type="text" onChange={this.onChangePriorInput} value={this.state.priorInput}/>
                  </div>
                  <button onClick={this.onAddInput} className="button-add">Add</button>
                  { todolist &&
                  <ul className="todo-list">
                    {todolist.map(each=>{
                      return (< XYZ name={each.name} id={each.id} click={each.click} onDelete={this.onDelete} onEdit={this.onEdit} onSave={this.onSave} onCancel={this.onCancel}/>
                      )
                    })}
                  </ul>
                  }
              </div>
              </div>
              
          </div>
          
       </div>
    )
  }
}
class Diary extends Component{
  state={DiaryList:(JSON.parse(localStorage.getItem("DairyMaterial"))==null)?[]:JSON.parse(localStorage.getItem("DairyMaterial")),addClick:false,searchInput:''}
  onAdd=()=>{
     this.setState({addClick:true})
  }
  onCancel=()=>{
    this.setState({addClick:false})
  }
  onSave=(textBoxContent,date)=>{
    console.log(date)
    const arr=this.state.DiaryList
    arr.push({date:date,content:textBoxContent,id:arr.length})
    localStorage.setItem("DairyMaterial",JSON.stringify(arr))
    this.setState({DiaryList:arr,addClick:false})
  }
  onChangeSearchInput = event => {
  this.setState({
    searchInput: event.target.value
  })
  } 
  onDelete=(id)=>{
    const arr=this.state.DiaryList;
    for (let i=id;i<arr.length;i++){
      arr[i].id--;
    }
    arr.splice(id,1);
    localStorage.setItem("DairyMaterial",JSON.stringify(arr))
    this.setState({DiaryList:arr,addClick:false})
  }
  render(){
    const {addClick,searchInput}=this.state
    const stringifiedTodoList = localStorage.getItem("DairyMaterial");
    const temp=JSON.parse(stringifiedTodoList)
    const DiaryList=temp;
    console.log(DiaryList)
    let searchResults=[]
    if(DiaryList!=null){
     searchResults = DiaryList.filter(each =>
      each.date.includes(searchInput)
    )}
    const displayResults=[]
    const size=searchResults.length
    for(let i=size-1;i>=0&&i>=size-3;i--)
    {
       displayResults.push(searchResults[i]);
    }
    console.log('displayResults',displayResults)

    return(
      <div className="diary-bg-container">
        <div className="search-add-row">
          < PQ addClick={addClick} onAdd={this.onAdd} onSave={this.onSave} onCancel={this.onCancel} />
          <input
            type="search"
            onChange={this.onChangeSearchInput}
            value={searchInput}
            className="search-the-date"
            placeholder="search-by-date"
          />
        </div>
        <div className="days-container">
            {displayResults.map(each=>{
            return(
            <DayContent date={each.date} content={each.content} id={each.id} onDelete={this.onDelete}/>
             )})}
            
        </div>
      </div>
    )
  }
}
const DayContent=props=>{
  const {date,content,id,onDelete}=props
  const Delete=()=>{
    onDelete(id);
  }
  return(
    <div>
        <div className="diary-day-content">
          <div className="delete-icon-align">
             <h1 className="today-date">{date}</h1>
             <AiFillDelete onClick={Delete} size={22}/>
          </div>
          <p className="today-day-content">{content}</p>
        </div>
       
    </div>
  )
}

export default App