import {Component} from 'react' 
import './index.css'
import {TiCancel} from 'react-icons/ti'
import {VscSaveAs} from 'react-icons/vsc'
class PQ extends Component{
    state={textBoxContent:"",date:""}
    Add=()=>{
        this.props.onAdd();
    }
    Save=()=>{
        console.log(this.state.textBoxContent)
        const text=this.state.textBoxContent
        const date=this.state.date
        this.state.textBoxContent=''
        this.state.data=''
        this.props.onSave(text,date);
    }
    Cancel=()=>{
        this.props.onCancel();
    }
    onChangeTextBox=event=>{
        this.setState({
            textBoxContent: event.target.value,
          })
    }
    onChangeDate=event=>{
        this.setState({
            date: event.target.value,
          })
    }
    render(){
        return(
           <div className="add-the-day-container">
               {!this.props.addClick?
              <button onClick={this.Add} className="button-add-diary">Add</button>:<Card Save={this.Save} Cancel={this.Cancel}onChangeTextBox={this.onChangeTextBox} textBoxContent={this.textBoxContent} onChangeDate={this.onChangeDate} date={this.date}/>}
           </div>
        )
    }
}
const Card=props=>{
    const {Save,onChangeTextBox,textBoxContent,onChangeDate,date,Cancel}=props
    return(
       <div className="add-diary">
           <div>
             <input type="text" className="date-add"onChange={onChangeDate} value={date}
              placeholder="DD/MM/YYYY"/>
           </div>
          <textarea className="textArea" placeholder="write your day here"
          className="add-day"
          onChange={onChangeTextBox} value={textBoxContent}/> 
          <div className="diary-cancel-save-button">
            <div className="zy">
               <TiCancel onClick={Cancel} size={30}/>
            </div>
            <div className="zy">
                <VscSaveAs onClick={Save} size={30}/>
            </div>
          </div>
       </div>
    )
}

export default PQ